<template>
  <div class="d-flex flex-column vh-100">
    <div class="page py-4" style="overflow-x:hidden;">
      <div class="iphoneframe-container" ref="iphone">
        <router-link to="/account">
          <BackButton />
        </router-link>
        <transition name="fade-top">
          <PreviewCardWithIphoneFrame
            class="w-100"
            :image="previewCardImage"
            :title="previewCardTitle"
            :titleMyan="previewCardTitleMyan"
            :desc="previewCardDesc"
            :descMyan="previewCardDescMyan"
            :isFindOther="true"
            :isContactUs="true"
            type="service"
            firstButtonText="Repair now"
            secondButtonText="Connect with us"
            thirdButtonText="Contact us"
            v-show="showPreview"
          />
        </transition>
        <div class="mt-4 w-100 d-flex justify-content-center" v-if="showPreview">
          <button
            class="btn btn-circle"
            style="width: 40px; height: 40px;background:#B7B7B7;"
            @click="showPreview = false"
          >
            <CloseIcon class="close-icon" />
          </button>
        </div>

        <div class="mt-4 w-100 title">
          <StageTitle stageNum="5" :title="stageInfoText.title" :desc="stageInfoText.desc" />
        </div>
        <div class="mt-4 w-100 p-3">
          <AddedForm
            v-model="serviceInfo"
            :info="info"
            type="service"
            @add="addServiceInformation"
            @uploadPhoto="uploadPhoto"
          />
        </div>
        <div class="w-100 d-flex justify-content-center">
          <div class="d-flex flex-center btn btn-primary" @click="Preview()">
            <EyeIcon class="eye-icon" />
            <span class="ml-1">Preview</span>
          </div>
        </div>
        <!--<div class="mt-2 w-100 py-4 px-5">
          <MobileToggle
            :title="toggles.askCustomerInfomation.title"
            :desc="toggles.askCustomerInfomation.desc"
            v-model="isEnabledAskCustomerInfo"
            @change="addShopNature"
          />
        </div>-->
        <div class="mt-4 w-100 d-flex flex-center">
          <router-link to="/account/technicalsupports">
            <ForwardButton @click.native="$emit('showPage',1)" />
          </router-link>
        </div>
      </div>
    </div>
    <bottom-navigation-bar />
    <Loading v-if="showLoading" />
    <AlertBox v-if="!shopExist" :alertInfo="alertInfo" @goto="route('/account/contactinfo')" />
  </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import ForwardButton from "@/components/ForwardButton";
import CloseIcon from "@/assets/icons/Close";
import EyeIcon from "@/assets/icons/Eye";
import StageTitle from "@/components/StageTitle";
//import MobileToggle from "@/components/MobileToggle";
import AddedForm from "@/components/AddedForm";
import axios from "axios";
import BottomNavigationBar from "@/components/BottomNavigationBar";
import { debounce } from "lodash";
import AlertBox from "@/components/AlertBox";
import Loading from "@/components/Loading";

export default {
  components: {
    PreviewCardWithIphoneFrame,
    StageTitle,
    //MobileToggle,
    BackButton,
    ForwardButton,
    AddedForm,
    CloseIcon,
    Loading,
    EyeIcon,
    AlertBox,
    BottomNavigationBar
  },
  data() {
    return {
      showLoading: true,
      showPreview: false,
      pageActive: true,
      isEnabledAskCustomerInfo: false,
      photoUploading: false,
      serviceID:'',
      previewCardDefaults: {
        title: "Your service information is awesome",
        desc:
          "Let's us know your informataion on our service.We do appreciate it",
        image:
          "https://portalvhdslvb28rs1c3tmc.blob.core.windows.net/yammo/allstaricon/picture.png"
       },
      stageInfoText: {
        title: "'Service' information",
        desc: "Fill your service information once"
      },
      toggles: {
        askCustomerInfomation: {
          title: "Ask Customer Information",
          desc:
            "Enable this option will ask the customer name, contact number and address."
        }
      },
      serviceInfo: {
        title: "",
        desc: "",
        image: "",
        titleMyan: "",
        descMyan: "",
        fbPageUrl: "",
      },
      info: {
        title: "Type 'Service information' title (Eng)",
        titleMyan: "Type 'Service information' title (Myan)",
        desc: "Type 'Service information' message (Eng)",
        descMyan: "Type 'Service information' message (Myan)",
        photoText: "add 'Service information' image",
        fbPageUrl: "Type Facebook Page url",
        type: "ServiceInformation",
        buttons: [
          {
            title: "Repair now"
          },
          {
            title: "Connect with us"
          },
          {
            title: "Contact us"
          }
        ]
      },
      alertInfo: {
        title: "Alert",
        msg:
          "To fill your service information,you need to add contact information first.",
        buttons: [
          {
            text: "Add Shop",
            action: "goto"
          }
        ]
      } 
    };
  },
  methods: {
    addServiceInformation: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.serviceID,
        Title: this.serviceInfo.title,
        Message: this.serviceInfo.desc,
        ShopGuid: this.shopID
      };
      if (this.promoteID == "") {
        await axios.post("service/upsert", obj).then(res => {
          this.serviceID=res.data;
        });
      } else {
        await axios.put("service/upsert", obj);
      }
    }, 3000),

    async uploadPhoto(url) {
      let obj = {
        Guid: this.serviceID,
        Photo: url,
        ShopGuid: this.shopID
      };
      this.photoUploading = true;
      if (this.serviceID == "") {
        await axios.post("service/uploadphoto", obj).then(res => {
          this.photoUploading = false;
          this.serviceID=res.data;
        });
      } else {
        await axios.put("service/uploadphoto", obj).then(() => {
          this.photoUploading = false;
        });
      }
    },
    async getService() {
      if (this.shopID == "" || this.shopID == "0") return;
      await axios
        .get(
          `service?shopGuid=${this.shopID}&customerID=${this.customerID}`
        )
        .then(res => {
          if (res.data != null) {
            this.serviceInfo.title = res.data.tService.Title;
            this.serviceInfo.image = res.data.tService.PhotoUrl;
            this.serviceInfo.desc = res.data.tService.Message;
            this.serviceID=res.data.tService.Guid;
            this.isEnabledAskCustomerInfo = res.data.isEnabledAskCustInfo;
          }
          this.showLoading = false;
        });
    },
    addShopNature: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.shopID,
        CustomerID: this.customerID,
        IsEnabledCustomerInfoForServiceInfo: this.isEnabledAskCustomerInfo
      };
      await axios.put("shop/shopNatureFromStepFive", obj);
    }, 3000),
    validateForm() {
      if (
        this.serviceInfo.title == "" &&
        this.serviceInfo.desc == "" &&
        this.serviceInfo.image == ""
      ) {
        return false;
      }
      return true;
    },
    Preview() {
      this.$refs.iphone.scrollIntoView();
      this.showPreview = true;
    }
  },
  mounted() {
    this.getService();
  },
  watch:{
    shopID(){
      this.getService();
    }
  },
  computed: {
    previewCardTitle() {
      return this.serviceInfo.title
        ? this.serviceInfo.title
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.serviceInfo.desc
        ? this.serviceInfo.desc
        : this.previewCardDefaults.desc;
    },
    previewCardTitleMyan() {
      return this.serviceInfo.titleMyan
        ? this.serviceInfo.titleMyan
        : this.previewCardDefaults.title;
    },
    previewCardDescMyan() {
      return this.serviceInfo.descMyan
        ? this.serviceInfo.descMyan
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.serviceInfo.image
        ? this.serviceInfo.image
        : this.previewCardDefaults.image;
    },
    shopID() {
      return this.$store.state.shopID;
    },
    customerID() {
      return this.$store.state.customerID;
    },
    shopExist() {
      return this.shopID != "0";
    },
  }
};
</script>
<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_Utilities.scss";
.page {
  flex: 1;
  overflow-y: auto;
}
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -1.5rem;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables";
.title {
  padding-left: 0.8rem;
}
.btn-primary {
  border-radius: 40px;
  background-color: $lightBlue;
  border-color: unset;
  &:hover {
    border-color: unset;
    box-shadow: none;
  }
  &:active {
    border-color: unset;
    box-shadow: none;
  }
  &:focus {
    border-color: unset;
    box-shadow: none;
  }
}
.eye-icon {
  width: 0.9rem;
  height: auto;
  margin-top: -2px;
}
.btn-circle {
  width: 40px;
  height: 40px;
  background: $grayActive;
}
.close-icon {
  width: 0.8rem;
}
</style>
