  height: 100%;
  z-index:1000;
  background: rgba(0, 0, 0, 0.1);
}
.alert-box{
    justify-content: flex-end;
    position:absolute;
    width:100%;
    bottom: 0;
    background-color:$bgWhite;
    border-top-left-radius: 13px;
    border-top-right-radius:13px;
}
.title{
  font-size: 1.1rem;
  padding: 1rem;
  border-bottom: 1px solid $bgGray;
}
.description{
  padding: 1rem;
  border-bottom: 1px solid $bgGray;
}
.buttons{
